.rom-card {
  position: relative;
  width: 500px;
  height: 230px;
  padding: 15px;
  overflow: hidden;
  background: linear-gradient(60deg, #151516 0%, #1d2229 100%);
  margin-bottom: 15px;
  border-radius: 20px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}
.rom-card h2 {
  z-index: 99;
  position: absolute;
  bottom: 25px;
  right: 130px;
  margin-left: 30px;
  font-size: 30px;
  font-weight: 700;
  color: #fff;
  pointer-events: none;
  transition: 0.2s ease;
  direction: rtl;
  max-width: 300px;
  text-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
}
.rom-card p {
  z-index: 99;
  position: absolute;
  bottom: 30px;
  right: 130px;
  width: 150px;
  color: #fff;
  opacity: 0;
  font-size: 12px;
  text-align: right;
  letter-spacing: 1px;
  transition: 0.2s ease;
}
.rom-card span {
  z-index: 99;
  position: absolute;
  top: 60px;
  right: 35px;
  color: #fff;
  font-size: 12px;
  letter-spacing: 1px;
  writing-mode: vertical-lr;
  transition: 0.2s ease;
  opacity: 0.7;
}
.rom-card .pic {
  z-index: 100;
  width: 395px;
  height: 200px;
  background-size: cover;
  border-radius: 12px;
  filter: grayscale(100%);
  transition: 0.3s ease;
}
.rom-card button {
  position: absolute;
  right: 30px;
  bottom: 40px;
  width: 30px;
  height: 30px;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  outline: none;
  opacity: 1;
  transition: 0.3s ease;
}
.rom-card:hover button {
  transform: scale(16.5);
  opacity: 0.5;
}
.rom-card:hover h2 {
  bottom: 80px;
  right: 50px;
}
.rom-card:hover p {
  opacity: 1;
  right: 60px;
}
.rom-card:hover .pic {
  filter: grayscale(0);
  width: 470px;
}
@media (max-width: 768px) {
  .rom-card {
    width: 100%;
    margin-right: 40px;
  }
  .rom-card button {
    transform: scale(16.5);
    opacity: 0.5;
  }
  .rom-card h2 {
    bottom: 80px;
    right: 50px;
  }
  .rom-card p {
    opacity: 1;
    right: 60px;
  }
  .rom-card .pic {
    filter: grayscale(0);
    width: 470px;
  }
}

.roms-grid {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}